<template>
    <b-container fluid>
        <b-row class="pa20">
            <b-col lg="4" md="12" align-self="start" class="cityBox" >
                <b-col>
                    <img class="img-fluid" src="../assets/lima.jpg"  />
                 </b-col>
                 <b-col>
                     <h6>Current Temperature:</h6>
                    <h5>{{ lima.WeatherText }} and {{ limaTemp }}&#176; F</h5>
                    <p>
                        We'll make our way into Peru via Lima, the modern captial of Peru and third largest city in Central and South America.
                        From here, we'll make our way north to Iquitos for the Amazon Basin part of the trip and return here when we've finished in the Highlands.
                        On our last day we'll do a tour of the city before making our way back to the United States.
                    </p>
                </b-col>
            </b-col>
             <b-col lg="4" md="12" align-self="start" class="cityBox">
                <b-col>
                    <img class="img-fluid" src="../assets/iquitos.jpg"  />
                 </b-col>
                 <b-col>
                    <h6>Current Temperature:</h6>
                    <h5>{{ iquitos.WeatherText }} and {{ iquitosTemp }}&#176; F</h5>
                    <p>
                        Iquitos is the largest metropolis in the Peruvian Amazon and the largest city in the world
                        that cannot be reached by road. After flying into Iquitos, we'll make our way down the 
                        Amazon River and make stops at the Explorma Lodge, ExplorNapo Lodge, and Ceiba Tops. 
                    </p>
                    <p>
                        Amazon Basin highlights include: guided jungle walks, boating, bird watching and canopy walks, visits to ethnobotanical gardens, an animal sanctuary, blackwater lakes, as well as visits to Iquitos, a local village/school and markets.  
                    </p>
                </b-col>
            </b-col>
             <b-col lg="4" md="12" align-self="start" class="cityBox">
                 <b-col>
                    <img class="img-fluid" src="../assets/cusco.jpg"  />
                 </b-col>
                 <b-col>
                     <h6>Current Temperature:</h6>
                    <h5>{{ cusco.WeatherText }} and {{ cuscoTemp }}&#176; F</h5>
                    <p>
                        The Highlands portion of the trip begins in Cusco, capital of the Incan Empire until the conquest of the Spanish. Located in the Andes mountains, Cusco is an important cultural center filled with lots of unique
                        traveling oppourtunities. We will make day trips into nearby ruins, visit the local Pisac Market, the Pottery Factory, salt mines, and the world famous Machu Picchu, Huayna Picchu, and Ollantaytambo.
                    </p>
                 </b-col>
            </b-col>
        </b-row>
    </b-container>
</template>
<script>
//https://developer.accuweather.com/weather-icons weather icon lists
const axios = require('axios');

export default {
    data: function() {
    return {
     lima:Object,
     limaTemp: 0,
     iquitos: Object,
     iquitosTemp: 0,
     cusco: Object,
     cuscoTemp: 0,
     slide:0,
     sliding:null,
    }
  },
    mounted() {
    axios
      .get("https://dataservice.accuweather.com/currentconditions/v1/264120",{
          params:{apikey: 'pmrOsR1WpIh184aRZWNG5W7Yko0ODurt'}
      })
      .then(response => {
            this.lima =response.data[0] 
            this.limaTemp = response.data[0].Temperature.Imperial.Value;
        })
      //.catch(error => console.log(error));

      axios.get('https://dataservice.accuweather.com/currentconditions/v1/258505', {
          params:{apikey: 'pmrOsR1WpIh184aRZWNG5W7Yko0ODurt'}
      })
      .then(response => {
        this.iquitos = response.data[0]
        this.iquitosTemp = response.data[0].Temperature.Imperial.Value
      })
      //.catch(error => console.log(error))

      axios.get('https://dataservice.accuweather.com/currentconditions/v1/258505', {
          params:{apikey: 'pmrOsR1WpIh184aRZWNG5W7Yko0ODurt'}
      })
      .then(response => {
            this.cusco = response.data[0]
            this.cuscoTemp = response.data[0].Temperature.Imperial.Value
        })
      //.catch(error => console.log(error))
  }
}
</script>
<style>
.weatherText{padding:20px;}
.weatherText:nth-child(odd){text-align: right;}
.weatherText:nth-child(even){text-align: left;}

.cityBox {padding:20px}
.cityBox img{box-shadow: 0 0 5px rgb(106, 90, 64);}
.cityBox h3{margin:0.8rem 0;}
.cityBox p{font-size: 1.2rem; text-align: left}
.cityBox h6{margin:.5rem 0; text-align: center}

@media (max-width: 768px) {
    .weatherText:nth-child(odd), .weatherText:nth-child(even){
        text-align:center;
    }
}
</style>
