<template>
  <b-container style="padding:0">
    <!--start here -->
    <v-speed-dial
      v-model="fab"
      bottom
      right
      fixed
    >
      <template v-slot:activator>
        <v-btn
          v-model="fab"
          color="blue darken-2"
          dark
          fab
        >
          <v-icon v-if="fab">mdi-close</v-icon>
          <v-icon v-else>mdi-menu</v-icon>
        </v-btn>
      </template>
      <v-btn
        fab
        dark
        small
        color="green"
        @click="emailme = !emailme"
      >
        <v-icon>mdi-email</v-icon>
      </v-btn>
      <v-btn
        fab
        dark
        small
        color="indigo"
        @click="itinerary = !itinerary"
      >
        <v-icon>mdi-calendar-month-outline</v-icon>
      </v-btn>
      <v-btn
        fab
        dark
        small
        color="red"
        @click="slideshow = !slideshow"
      >
        <v-icon>mdi-image-multiple</v-icon>
      </v-btn>
    </v-speed-dial>
    <!-- end speed dial here-->
    <!-- popups -->
    <v-dialog v-model="emailme" max-width="500px">
      <v-card>
        <v-card-text>
         <p>If you have any questions about the trip, want to share photos, or your experiences on the trip, feel free to contact us at: </p>
         <a href="mailto:dr.longhofer@gmail.com">dr.longhofer@gmail.com</a>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-dialog v-model="itinerary" max-width="500px">
      <v-card>
        <v-card-text>
         <p>Itinerary 2019</p>
          <a href="./itinerary-2019.pdf" target="_blank">Download Here(PDF)</a>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-dialog v-model="slideshow" max-width="500px">
      <v-card>
        <v-card-text>
         <p>Download Our 2020 Slideshow</p>
         <a href="./informational-meeting-2020.pdf" target="_blank">Download Here(PDF)</a>
        </v-card-text>
      </v-card>
    </v-dialog>

  </b-container>
</template>
<script>
  export default {
    data: () => ({
      fab: false,
      fling: false,
      hover: false,
      tabs: null,
      emailme: false,
      itinerary: false,
      slideshow: false,
      transition: 'slide-y-reverse-transition',
    }),

    computed: {
      activeFab () {
        switch (this.tabs) {
          case 'one': return { class: 'purple', icon: 'account_circle' }
          case 'two': return { class: 'red', icon: 'edit' }
          case 'three': return { class: 'green', icon: 'keyboard_arrow_up' }
          default: return {}
        }
      },
    },
  }
</script>

<style>
* i{ font-family: 'Material Icons'; }
</style>
